<template>
  <div>
    <div class="page__hd">
      <h1 class="page__title"></h1>
      <p class="page__desc"></p>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-loadmore" v-if="isLoading">
      <i class="weui-loading"></i>
      <span class="weui-loadmore__tips">...</span>
    </div>
    <div class="weui-cells">
      <a class="weui-cell weui-cell_access"
        :href="item.url"
        :key="item.offerId"
        v-for="item in items">
        <div class="weui-cell__bd">
          <p>{{item.name}}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a >
    </div>
  </div>
</template>

<script>
import Api from '../api'

export default {
  data () {
    return {
      isLoading: false,
      items: []
    }
  },

  created () {
    this.fetch()
  },

  methods: {
    fetch () {
      this.isLoading = true
      Api('/api/offers').then(({data}) => {
        this.isLoading = false
        this.items = data
      }).catch(() => {
        this.isLoading = false
      })
    }
  }
}
</script>

<style scoped>
.page__hd {
  padding: 40px;
}
.page__title {
  text-align: left;
  font-size: 20px;
  font-weight: 400;
}
.page__desc {
  margin-top: 5px;
  color: #888;
  text-align: left;
  font-size: 14px;
}
</style>



